<template>
    <div id="map" style="height:100%; width:100%">
    </div>
</template>
  
<script>
  import L from 'leaflet';
  import 'leaflet/dist/leaflet.css';
  import 'leaflet.heat'

  import markerIcon2x from 'leaflet/dist/images/marker-icon-2x.png';
  import markerIcon from 'leaflet/dist/images/marker-icon.png';
  import markerShadow from 'leaflet/dist/images/marker-shadow.png';

  delete L.Icon.Default.prototype._getIconUrl;

L.Icon.Default.mergeOptions({
  iconRetinaUrl: markerIcon2x,
  iconUrl: markerIcon,
  shadowUrl: markerShadow,
});

  export default {
    name: 'LeafletMap',
    mounted(){
      const map = L.map('map').setView([53.4808, -2.2426], 17);

      L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
        attribution: '&copy; OpenStreetMap contributors',
      }).addTo(map);

      const marker1 = L.marker([53.4808, -2.2426])
        .addTo(map)
        .bindPopup('53.4808, -2.2426');

        marker1.on('click', () => {
    this.$router.push('/device1'); // or use window.location.href = '/page-a' if not using Vue Router
          
  });

  const legend = L.control({ position: 'bottomright' });

legend.onAdd = function () {
  const div = L.DomUtil.create('div', 'info legend');
  div.innerHTML = `
    <h4>Legend</h4>
    <i style="background: red"></i> THD>10% <br>
    <i style="background: yellow"></i> THD<10% <br>
    <i style="background: blue"></i> THD<5% <br>
  `;
  return div;
};

legend.addTo(map);

  

      /*const marker2 = L.marker([53.4800, -2.2430])
        .addTo(map)
        .bindPopup('53.4800, -2.2430');*/

        /*marker2.on('click', () => {
    this.$router.push('/device2'); // or use window.location.href = '/page-a' if not using Vue Router
  });*/

      //Heatmap data: [lat, lngm intensity]
      const heatData = [

      [53.4808, -2.2426, 0.91],
      //[53.4800, -2.2430, 0.8],
      //[51.507, -0.08, 0.2],
      //[51.503, -0.095, 0.6],
      
      ];

      L.heatLayer(heatData, {
        radius: 25,
        blur: 20,
        maxZoom: 17,
      }).addTo(map);

      setTimeout(() => {
    map.invalidateSize();
  }, 300);

    window.addEventListener('resize', this.handleResize);
    window.addEventListener('orientationchange', this.handleResize);
    },
    methods: {
    handleResize() {
      if (this.map) {
        setTimeout(() => {
          this.map.invalidateSize();
        }, 300); // Delay ensures layout has updated
      }
    }
  },
  beforeUnmount() {
    // ðŸ§¹ Clean up listeners
    window.removeEventListener('resize', this.handleResize);
    window.removeEventListener('orientationchange', this.handleResize);

    if (this.map) {
      this.map.remove(); // Properly remove the map
      this.map = null;
    }
  }
  };
</script>
  
<style>
.info.legend {
  background: white;
  padding: 6px 8px;
  font: 14px Arial, sans-serif;
  box-shadow: 0 0 15px rgba(0,0,0,0.2);
  border-radius: 5px;
  line-height: 18px;
  color: #555;
}
.info.legend i {
  width: 18px;
  height: 18px;
  float: left;
  margin-right: 8px;
  opacity: 0.7;
}
</style>